<template>
  <view class="tasks-container">
    <!-- È°∂ÈÉ®ÁªüËÆ°Âç°Áâá -->
    <view class="stats-cards">
      <view class="stat-card">
        <text class="stat-number">{{ taskStats.total }}</text>
        <text class="stat-label">ÊÄª‰ªªÂä°</text>
      </view>
      <view class="stat-card">
        <text class="stat-number">{{ taskStats.completed }}</text>
        <text class="stat-label">Â∑≤ÂÆåÊàê</text>
      </view>
      <view class="stat-card">
        <text class="stat-number">{{ taskStats.pending }}</text>
        <text class="stat-label">ÂæÖÂÆåÊàê</text>
      </view>
    </view>

    <!-- ÊêúÁ¥¢ÂíåÁ≠õÈÄâ -->
    <view class="search-section">
      <u-search
        v-model="searchKeyword"
        placeholder="ÊêúÁ¥¢‰ªªÂä°..."
        @search="handleSearch"
        @clear="handleClearSearch"
        class="search-input"
      ></u-search>
    </view>

    <!-- ‰ªªÂä°ÂàóË°® -->
    <view class="task-list">
      <view v-if="tasks.length === 0" class="empty-state">
        <text class="empty-icon">üìù</text>
        <text class="empty-text">ÊöÇÊó†‰ªªÂä°</text>
        <text class="empty-desc">ÊÇ®ËøòÊ≤°Êúâ‰ªª‰Ωï‰ªªÂä°</text>
      </view>

      <view v-else>
        <view
          v-for="task in filteredTasks"
          :key="task.id"
          class="task-item"
          :class="{ completed: task.completed }"
        >
          <view class="task-content">
            <view class="task-header">
              <text class="task-title">{{ task.title }}</text>
              <view class="task-priority" :class="task.priority">
                {{ getPriorityText(task.priority) }}
              </view>
            </view>

            <text v-if="task.description" class="task-desc">
              {{ task.description }}
            </text>

            <view class="task-meta">
              <text v-if="task.dueDate" class="task-due">
                üìÖ {{ formatDate(task.dueDate) }}
              </text>
              <text class="task-category">{{ task.category }}</text>
            </view>
          </view>

          <view class="task-actions">
            <u-button
              type="primary"
              size="mini"
              @click="toggleTaskComplete(task.id)"
            >
              {{ task.completed ? "Êú™ÂÆåÊàê" : "ÂÆåÊàê" }}
            </u-button>
            <u-button type="error" size="mini" @click="deleteTask(task.id)">
              Âà†Èô§
            </u-button>
          </view>
        </view>
      </view>
    </view>
  </view>
</template>

<script setup lang="ts">
import { ref, computed, onMounted } from "vue";

// ÂìçÂ∫îÂºèÊï∞ÊçÆ
const searchKeyword = ref("");
const tasks = ref([
  {
    id: 1,
    title: "ÂÆåÊàêÈ°πÁõÆÊñáÊ°£",
    description: "ÁºñÂÜôAPIÊñáÊ°£ÂíåÁî®Êà∑ÊâãÂÜå",
    completed: false,
    priority: "high",
    dueDate: "2024-01-30",
    category: "Â∑•‰Ωú",
    createTime: new Date(),
  },
  {
    id: 2,
    title: "Ë¥≠‰π∞ÁîüÊ¥ªÁî®ÂìÅ",
    description: "ÂéªË∂ÖÂ∏ÇË¥≠‰π∞Êó•Áî®ÂìÅ",
    completed: true,
    priority: "medium",
    dueDate: "2024-01-28",
    category: "ÁîüÊ¥ª",
    createTime: new Date(),
  },
]);

// ËÆ°ÁÆóÂ±ûÊÄß
const taskStats = computed(() => {
  const total = tasks.value.length;
  const completed = tasks.value.filter((task) => task.completed).length;
  const pending = total - completed;
  return { total, completed, pending };
});

const filteredTasks = computed(() => {
  if (!searchKeyword.value) return tasks.value;
  return tasks.value.filter(
    (task) =>
      task.title.toLowerCase().includes(searchKeyword.value.toLowerCase()) ||
      task.description.toLowerCase().includes(searchKeyword.value.toLowerCase())
  );
});

// ÊñπÊ≥ï
const handleSearch = () => {
  // ÊêúÁ¥¢ÈÄªËæëÂ∑≤Âú®ËÆ°ÁÆóÂ±ûÊÄß‰∏≠Â§ÑÁêÜ
};

const handleClearSearch = () => {
  searchKeyword.value = "";
};

const toggleTaskComplete = (taskId: number) => {
  const task = tasks.value.find((t) => t.id === taskId);
  if (task) {
    task.completed = !task.completed;
  }
};

const deleteTask = (taskId: number) => {
  uni.showModal({
    title: "Á°ÆËÆ§Âà†Èô§",
    content: "Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™‰ªªÂä°ÂêóÔºü",
    success: (res) => {
      if (res.confirm) {
        tasks.value = tasks.value.filter((t) => t.id !== taskId);
        uni.showToast({
          title: "Âà†Èô§ÊàêÂäü",
          icon: "success",
        });
      }
    },
  });
};

const getPriorityText = (priority: string) => {
  const map: Record<string, string> = {
    low: "‰Ωé",
    medium: "‰∏≠",
    high: "È´ò",
    urgent: "Á¥ßÊÄ•",
  };
  return map[priority] || "‰∏≠";
};

const formatDate = (dateStr: string) => {
  const date = new Date(dateStr);
  return date.toLocaleDateString("zh-CN");
};

onMounted(() => {
  uni.__f__('log','at pages/tasks/tasks.vue:176',"‰ªªÂä°È°µÈù¢Âä†ËΩΩÂÆåÊàê");
});
</script>

